import {Raycaster, DoubleSide, Vector2, Vector3, BufferGeometry, Line, LineBasicMaterial, LineDashedMaterial,
  PlaneGeometry, Mesh, MeshLambertMaterial, BoxGeometry, Group} from 'three'
import { scaleLinear, scaleBand } from 'd3-scale'
import { CSS3DObject } from "three/examples/jsm/renderers/CSS3DRenderer"
import CommonBar, {transformSeries, getMaxAndMin, getSeriesStackNum, commonOpts, createBufferAttribute} from './common'
import merge from 'lodash/merge'

export default class Bar extends CommonBar{
  constructor(el, opts={}) {
    super()
    this.el = el
    el.style.position = 'relative'
    this.opts = merge({}, commonOpts, opts)
    const {grid3D, xAxis, series} = this.opts
    // 
    this.canvasWidth = el.clientWidth
    this.canvasHeight = el.clientHeight
    // 3d
    this.xAxixWidth = grid3D.boxWidth || this.canvasWidth
    // 
    this.xData = xAxis.data
    // series
    transformSeries(series)
    this.setMaxAndMin()

    this.INTERSECTED = null
    this.TIPDOM = null
    this.raycaster = new Raycaster()
    this.pointer = new Vector2()

    this.sceneChildren = []
    this.textSceneChildren = []
    this.seriesStackNum = getSeriesStackNum(series)

    this.init()
  }

  init() {
    this.createScale()
    this.addScene()
    this.addLight()
    this.addCamera()
    this.createLegend()
    this.createXAxis()
    this.createYAxis()
    this.createBgPlane()
    // 
    this.createBarGroup()
    this.addRenderer()
    this.addControls()
    this.render()
    document.addEventListener('mousemove', this.onPointerMove)
    document.addEventListener('mouseleave', this.onMouseLeave)
    this.resize()
  }

  update(opts = {}) {
    this.opts = merge({}, commonOpts, opts)
    this.clear()
    const {grid3D, xAxis, series} = this.opts
    // 3d
    this.xAxixWidth = grid3D.boxWidth || this.canvasWidth
    // 
    this.xData = xAxis.data
    // 
    series.forEach(item => {
      if (item.originData) {
        item.data = item.originData
      }
    })
    // series
    transformSeries(series)
    this.setMaxAndMin()
    this.seriesStackNum = getSeriesStackNum(series)
    this.createScale()

    this.createLegend()
    this.createXAxis()
    this.createYAxis()
    this.createBgPlane()
    // 
    this.createBarGroup()
  }

  resize() {
    this.resizeObser = new ResizeObserver(() => {
      const {renderer, textRenderer, el, camera} = this
      this.canvasWidth = el.clientWidth
      this.canvasHeight = el.clientHeight

      // /1
      camera.aspect = this.canvasWidth / this.canvasHeight
      // .updateProjectionMatrix
      camera.updateProjectionMatrix()

      // 
      renderer.setSize(this.canvasWidth, this.canvasHeight)
      // 
      renderer.setPixelRatio(window.devicePixelRatio)

      // 
      textRenderer.setSize(this.canvasWidth, this.canvasHeight)
    })
    this.resizeObser.observe(this.el)
  }

  // 
  setMaxAndMin() {
    var allData = []
    this.opts.series.forEach(item => {
      allData = allData.concat(item.data)
    })
    const d = getMaxAndMin(allData)
    this.maxData = d.maxData
    this.minData = d.minData
  }

  //   
  getBarWidth () {
    // TODO: series.length
    let barW = 100 / this.seriesStackNum / 2 + '%'
    let {barWidth=barW, barGap=0} = this.opts.grid3D
    barWidth = typeof barWidth === 'number' ? barWidth : this.xWidth * 2 * parseFloat(barWidth) / 100
    barGap = typeof barGap === 'number' ? barGap : this.xWidth * parseFloat(barGap) /100
    return {barWidth, barGap}
  }

  // opts.grid
  getGripY() {
    return (this.opts.grid.top - this.opts.grid.bottom) / 2
  }

  createScale() {
    const { grid } = this.opts
    // 
    this.xScale = scaleBand().domain(this.xData).range([-this.xAxixWidth + grid.left*2, this.xAxixWidth - grid.right*2])
    // xScale xScale.bandwidth() / 2
    this.xWidth = this.xScale.bandwidth() / 2
    this.yScale = scaleLinear().domain([this.minData, this.maxData]).range([0, (this.canvasHeight - grid.top - grid.bottom) * 2])
    this.maxHeight = this.yScale(this.maxData)
    this.zeroHeight = this.yScale(0)
  }

  createXText(text, x, y, z=0) {
    const {opts: {xAxis}} = this
    const element = document.createElement('span')
    element.innerText = text
    element.style.color = xAxis.axisLabel.color
    element.style.fontSize = xAxis.axisLabel.fontSize + 'px'
    const object = new CSS3DObject( element )
    object.position.set(x, y, z)
    this.textScene.add( object )
  }

  // x
  createXAxis() {
    const {xScale, scene, xData, maxHeight, xWidth, opts: {xAxis}} = this
    if (!xAxis.show) return false

    // x
    if (xAxis.axisLine.show) {
      const points = []
      points.push( new Vector3( xScale(xData[0]), -maxHeight/2 - this.getGripY(), 0 ) )
      points.push( new Vector3( xScale(xData[xData.length-1]) + xWidth * 2, -maxHeight/2 - this.getGripY(), 0 ) )
      const geometry = new BufferGeometry().setFromPoints( points )
      const line = new Line( geometry, new LineBasicMaterial({
        color: xAxis.axisLine.lineStyle.color,
        linewidth: xAxis.axisLine.lineStyle.width
      }) )
      scene.add( line )
    }

    // x
    if (xAxis.axisLabel.show) {
      xData.forEach(item => {
        const y = -maxHeight/2 - this.getGripY() - 40
        this.createXText(item, xScale(item) + xWidth, y)
      })
    }
  }

  /**
   *
   * @param {*} text 
   * @param {*} y y
   */
  createYText(text, y, x) {
    const {maxData, maxHeight, xScale, xData, textScene, opts: {yAxis}} = this
    const element = document.createElement('span')
    const width = String(maxData).length * yAxis.axisLabel.fontSize * 2
    element.style.width = width + 'px'
    element.style.textAlign = 'right'
    element.style.color = yAxis.axisLabel.color
    element.style.fontSize = yAxis.axisLabel.fontSize + 'px'
    element.innerText = text
    const object = new CSS3DObject( element )
    x = x || xScale(xData[0]) - width / 2 - 20
    // xScale(xData[0]) - width / 2y20
    object.position.set(x, -maxHeight/2 + y - this.getGripY(), 0)
    textScene.add( object )
  }

  /**
   * y
   * @param {*} y y
   * @param {*} isDashed 
   */
  createYSplitLine(y, isDashed) {
    const {xScale, xData, maxHeight, xWidth, scene, opts: {yAxis}} = this
    if (!yAxis.splitLine.show) return false
    const {barWidth} = this.getBarWidth()
    const points = []
    const x = xScale(xData[0])
    points.push( new Vector3( x, -maxHeight/2 - this.getGripY() + y, 0 ) )
    points.push( new Vector3( x, -maxHeight/2  - this.getGripY() + y, 0 ) )
    points.push( new Vector3( x, -maxHeight/2  - this.getGripY() + y, -barWidth*2 ) )
    points.push( new Vector3( xScale(xData[xData.length-1]) + xWidth * 2, -maxHeight/2  - this.getGripY() + y, -barWidth*2 ) )
    const geometry = new BufferGeometry().setFromPoints( points )

    const line = new Line( geometry, isDashed ? new LineDashedMaterial({color: yAxis.splitLine.lineStyle.color, dashSize: 1}) : new LineBasicMaterial({color: yAxis.splitLine.lineStyle.color}))
    line.computeLineDistances()//
    scene.add( line )
  }

  createMarkLine(num, seriesIndex) {
    const {yScale, xScale, xData, maxHeight, scene, xWidth} = this
    const y = yScale(num), x = xScale(xData[0]), x2 = xScale(xData[xData.length - 1]) + xWidth * 2
    const points = []
    points.push( new Vector3(x, -maxHeight/2  - this.getGripY() + y, 0 ) )
    points.push( new Vector3(x2, -maxHeight/2  - this.getGripY() + y, 0 ) )
    const geometry = new BufferGeometry().setFromPoints( points )
    const line = new Line( geometry, new LineDashedMaterial({color: this.getColor(seriesIndex), dashSize: 1}))
    line.computeLineDistances()//
    scene.add( line )
    this.createYText(num,  y, x2)
  }

  // y
  createYAxis() {
    const {xScale, xData, maxHeight, scene, opts: {yAxis}} = this
    if (!yAxis.show) return false
    // y
    if (yAxis.axisLine.show) {
      const yPoints = []
      yPoints.push( new Vector3( xScale(xData[0]), -maxHeight/2 - this.getGripY(), 0 ) )
      yPoints.push( new Vector3( xScale(xData[0]), maxHeight/2 - this.getGripY(), 0 ) )
      const yLine = new Line( new BufferGeometry().setFromPoints( yPoints ), new LineBasicMaterial({
        color: yAxis.axisLine.lineStyle.color,
        linewidth: yAxis.axisLine.lineStyle.width
      }))
      scene.add( yLine )
    }
    // y
    this.addYText()
  }

  addYText() {
    const {maxData, minData, yScale, opts: {yAxis}} = this
    let tick = 4, tickNum = 0
    if (maxData >= 0 && minData === 0) {
      tickNum = maxData / tick
    } else if (maxData > 0 && minData < 0) {
      let negativeTick = 4
      // 
      if (maxData >= Math.abs(minData)) {
        // 
        tickNum = maxData / tick
        negativeTick = Math.abs(minData) / tickNum
      } else {
        // 
        tickNum = Math.abs(minData) / negativeTick
        tick = maxData / tickNum
      }
      for (let i=0; i <= negativeTick; i++) {
        const num = -(i * tickNum * 10000000000) / 10000000000
        if (yAxis.axisLabel.show) this.createYText(num, yScale(num))
        this.createYSplitLine(yScale(num), i > 0)
      }
    } else if (maxData === 0 && minData < 0) {
      tickNum = minData / tick
    }

    for (let i=0; i <= tick; i++) {
      const num = (i * tickNum * 10000000000) / 10000000000
      if (yAxis.axisLabel.show) this.createYText(num, yScale(num))
      this.createYSplitLine(yScale(num), i > 0)
    }
  }

  // 
  createBgPlane() {
    const {xData, maxHeight, scene, xWidth, opts: {grid, grid3D}} = this
    if (!grid3D.background.show) return false
    const {barWidth} = this.getBarWidth()
    const bgPlane = new PlaneGeometry(xWidth * 2 * xData.length, maxHeight)
    const materialOpts = {transparent: true, side: DoubleSide, ...grid3D.background}
    delete materialOpts.show // 
    const bgMesh = new Mesh(bgPlane, new MeshLambertMaterial(materialOpts))
    bgMesh.position.set(Math.abs(grid.left-grid.right),  -this.getGripY(), -barWidth*2)
    scene.add(bgMesh)
  }

  /**
   * 
   * @param {*} drawIndex x
   * @param {*} seriesIndex opt.series
   * @param {*} i 
   * @param {*} y y
   * @returns
   */
  createBar (drawIndex, seriesIndex, i) {
    const {xData, maxHeight, zeroHeight, yScale, opts} = this
    const {barWidth, barGap} = this.getBarWidth()
    const {itemStyle={}, data = [], originData = [], name} = opts.series[seriesIndex]
    // 
    const height = yScale(originData[i])
    // y
    const y = yScale(data[i] - originData[i])
    // 0
    const relativeHeight = height - zeroHeight
    const geometry = new BoxGeometry(barWidth, Math.abs(relativeHeight), barWidth)
    geometry.setAttribute('color', createBufferAttribute('#fff'))
    const material = new MeshLambertMaterial({ color: this.getColor(seriesIndex), vertexColors: true, transparent: true, ...itemStyle })
    const bar = new Mesh(geometry, material)
    // maxHeight - relativeHeight: y - 02y
    // yyyScale(0),zeroHeight.y0
    bar.position.set(drawIndex * (barWidth + barGap),  -(maxHeight - relativeHeight) / 2 + y - this.getGripY(), 0)
    bar.itemData = {
      x: xData[i],
      y: originData[i],
      name
    }
    if (opts.grid3D.barAngle) {
      bar.rotation.y = Math.PI * opts.grid3D.barAngle / 180
    }
    return bar
  }

  // 
  createBarGroup() {
    const {xData, xScale, xWidth, scene, opts} = this
    const stackNames = []
    //  xx
    const barGrops = Array.from({length: xData.length}, () => new Group())
    opts.series.forEach((item, seriesIndex) => {
      if (item.markLine) this.createMarkLine(item.markLine.value, seriesIndex)
      // stackNamesdrawIndexstack
      if (!stackNames.find(name => name === item.stack)) {
        if (item.stack) stackNames.push(item.stack)
        else stackNames.push(seriesIndex)
      }
      // drawIndexx
      const drawIndex = stackNames.findIndex(name => name === (item.stack || seriesIndex) )

      item.data.forEach((d, i) => {
        if (i < barGrops.length) {
          const bar = this.createBar(drawIndex, seriesIndex, i)
          barGrops[i].add(bar)
          // barGrops[i].barNumx
          barGrops[i].barNum = stackNames.length
        }
      })
    })

    // xscene
    const {barWidth, barGap} = this.getBarWidth()
    barGrops.forEach((g,i) => {
      // 
      let gWidth = 0
      Array.from({length: g.barNum}).forEach((mesh, i) => {
        // barGap()
        if (i === 0) {
          gWidth += barWidth
        } else {
          gWidth += (barGap + barWidth)
        }
      })
      const x = xScale(xData[i])
      // xtranslateX(-50%)x
      // x + barWidth / 2xgWidth / 2xxWidth
      g.position.set(x + barWidth / 2 - gWidth / 2 + xWidth,  0, -barWidth)
      scene.add(g)
    })
  }
}
